<template>
  <div>
    <fTitle>
      <template #title>Checkbox 多选框</template>
      <template #introduce>一组备选项中进行多选</template>
    </fTitle>
    <sTitle>
      <template #title>多选框组</template>
      <template #introduce>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row>
        <el-checkbox-group v-model="checkList1">
    <el-checkbox label="复选框 A"></el-checkbox>
    <el-checkbox label="复选框 B"></el-checkbox>
    <el-checkbox label="复选框 C"></el-checkbox>
    <el-checkbox label="禁用" disabled></el-checkbox>
    <el-checkbox label="选中且禁用" disabled></el-checkbox>
  </el-checkbox-group>
</el-row>
<el-row type="flex" justify="space-around">
  <el-col :span="10"><div class="grid-content"><el-button @click="getCheck1=checkList1" type="success">获取多选框check</el-button></div></el-col>
  <el-col :span="10"><div class="grid-content" style="border-bottom: 2px dashed pink;"><span style="line-height: 35px;">{{ getCheck1.join('|') }}</span></div></el-col></el-row>
      </div></div>
      <sTitle>
      <template #title>indeterminate 状态</template>
      <template #introduce>indeterminate 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果</template>
    </sTitle>
    <div class="outbox"><div class="inbox">
      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
  <div style="margin: 15px 0;"></div>
  <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
    <el-checkbox v-for="city in cities" :label="city" :key="city">{{ city }}</el-checkbox>
  </el-checkbox-group>
  </div></div>
  <sTitle>
      <template #title>可选项目数量的限制</template>
      <template #introduce>使用 min 和 max 属性能够限制可以被勾选的项目的数量。</template>
    </sTitle>
    <div class="outbox"><div class="inbox">
      <el-checkbox-group
    v-model="checkedCities1"
    :min="1"
    :max="2">
    <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
  </el-checkbox-group>
  </div></div>
  </div>

</template>

<script>
const cityOptions = ['上海', '北京', '广州', '深圳']
export default {
  name: 'ColorIndex',
  data () {
    return {
      checkList1: ['复选框 A', '复选框 B', '选中且禁用'],
      getCheck1: [],
      checkAll: false,
      checkedCities: ['上海', '北京'],
      checkedCities1: ['广州', '深圳'],
      cities: cityOptions,
      isIndeterminate: true
    }
  },
  methods: {
    handleCheckAllChange (val) {
      console.log(val)
      console.log(this.checkAll)
      this.checkedCities = val ? cityOptions : []
      this.isIndeterminate = false
      // 这里的value形参绑定的选择组等于自己所绑定的V-model的值
      // val等于单独一个选框选择后的值，只为true或false,然后该值与checkAll双向绑定
      // 点击全选框是val影响checkAll
    },
    handleCheckedCitiesChange (value) {
      // 两个事件分别点击时候才触发
      // isIndeterminate只控制全选按钮的混合值，即选中一部分为true,全选和全不选都为false
      const checkedCount = value.length
      this.checkAll = checkedCount === this.cities.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles//row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      padding: 20px;
      .el-checkbox{
        zoom: 130%;
      }

      .grid-content{
        height: 50px;
    background: white;
    margin-top: 20px;
        .el-button{
          font-size: 20px;
        }
      }}}

</style>
